<template>
	<div class="home_view">
		<div class="projectTitle">欢迎使用 {{projectName}}</div>
		<div class="count_list">
			<el-collapse-transition v-if="btnAuth('minsuxinxi','首页总数')">
				<el-card v-show="countTypeList.closeminsuxinxiCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								民宿信息
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenminsuxinxiCountType')" class="showIcons"
									:class="countTypeList.hiddenminsuxinxiCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closeminsuxinxiCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenminsuxinxiCountType">
							<div class="count_title">民宿信息总数</div>
							<div class="count_num">{{minsuxinxiCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('minsuyuding','首页总数')">
				<el-card v-show="countTypeList.closeminsuyudingCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								民宿预订
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenminsuyudingCountType')" class="showIcons"
									:class="countTypeList.hiddenminsuyudingCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closeminsuyudingCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenminsuyudingCountType">
							<div class="count_title">民宿预订总数</div>
							<div class="count_num">{{minsuyudingCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('minsuxinxi','首页统计')">
				<el-card v-show="cardTypeList.closeminsuxinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								民宿信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenminsuxinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenminsuxinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeminsuxinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenminsuxinxiChartType1">
							<div id="minsuxinxifangyuanzhuangtaiEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('minsuyuding','首页统计')">
				<el-card v-show="cardTypeList.closeminsuyudingChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								民宿预订
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenminsuyudingChartType1')" class="showIcons"
									:class="cardTypeList.hiddenminsuyudingChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closeminsuyudingChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenminsuyudingChartType1">
							<div id="minsuyudingruzhujineEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closeminsuxinxiCountType = true
		countTypeList.value.hiddenminsuxinxiCountType = true
		if(btnAuth('minsuxinxi','首页总数')){
			getminsuxinxiCount()
		}
		countTypeList.value.closeminsuyudingCountType = true
		countTypeList.value.hiddenminsuyudingCountType = true
		if(btnAuth('minsuyuding','首页总数')){
			getminsuyudingCount()
		}
	}
	const minsuxinxiCount = ref(0)
	const getminsuxinxiCount = () => {
		context?.$http({
			url:'minsuxinxi/count',
			method: 'get'
		}).then(res=>{
			minsuxinxiCount.value = res.data.data
		})
	}
	const minsuyudingCount = ref(0)
	const getminsuyudingCount = () => {
		context?.$http({
			url:'minsuyuding/count',
			method: 'get'
		}).then(res=>{
			minsuyudingCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closeminsuxinxiChartType1: true,
		hiddenminsuxinxiChartType1: true,
		closeminsuyudingChartType1: true,
		hiddenminsuyudingChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('minsuxinxi','首页统计')){
			getminsuxinxiChart1()
		}
		if(btnAuth('minsuyuding','首页统计')){
			getminsuyudingChart1()
		}
	}
	const getminsuxinxiChart1 = () => {
		nextTick(()=>{
			var fangyuanzhuangtaiEchart1 = echarts.init(document.getElementById("minsuxinxifangyuanzhuangtaiEchart1"),'macarons');
			context?.$http({
				url: "minsuxinxi/group/fangyuanzhuangtai",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].fangyuanzhuangtai);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].fangyuanzhuangtai
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '房源状态统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				fangyuanzhuangtaiEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				fangyuanzhuangtaiEchart1.setOption(option);
				//根据窗口的大小变动图表
				fangyuanzhuangtaiEchart1.resize();
			})
		})
	}
	const getminsuyudingChart1 = () => {
		nextTick(()=>{
			var ruzhujineEchart1 = echarts.init(document.getElementById("minsuyudingruzhujineEchart1"),'macarons');
			context?.$http({
				url: `minsuyuding/value/fangjianmingcheng/ruzhujine`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].fangjianmingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].fangjianmingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '房源销售统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				ruzhujineEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				ruzhujineEchart1.setOption(option);
				//根据窗口的大小变动图表
				ruzhujineEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 30px;
		justify-content: center;
		align-items: center;
		height: 50%;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			margin: 0 10px 10px;
			flex: 1;
			width: 100%;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					text-align: center;
					// 总数标题
					.count_title{
						color: #666;
						font-size: 16px;
						line-height: 2;
					}
					// 总数数字
					.count_num{
						color: #F00;
						font-size: 30px;
						line-height: 2;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 20px;
		background: url(http://clfile.zggen.cn/20240203/09a98f4852104a599055d47953ef0a23.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 1px solid #e4e7ed;
			box-shadow: 0px 0px 12px rgba(0,0,0,.12);
			margin: 0 10px 10px;
			flex: 1;
			width: 100%;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border: 1px solid #e4e7ed;
				width: 100%;
				border-width: 0 0 1px;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #f00;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #aaa;
							font-size: 20px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
